.works-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.work-image {
  padding: 10px;
  width: 80%;
  justify-content: center;
}

.project-link {
  display: block;
  padding: 15px;
  margin: 10px;
  border: 2px solid #3498db;
  border-radius: 8px;
  text-decoration: none;
  color: #3498db;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s, color 0.3s;
}

.project-link:hover {
  background-color: #3498db;
  color: #fff;
}
